.login {
  height: 100vh;
}
.login-bg {
  position: relative;
  width: 100%;
  height: 100%;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  margin-left: auto;
  margin-right: auto;
}
.login-container {
  width: 300px;
  height: 340px;
  background-color: rgba(0, 0, 0, .4);
  position: absolute;
  top: calc(50% - 150px);
  left: calc(50% - 170px);
  color: #ccc;
  padding: 70px 20px;
}
